import { parseValuesFromImagesAndUpdateForm, postRequestBinary } from "@/api/api";
import { extractImageFromPrediction, makeblob } from "@/utils/imageUtils";
import * as CocoSSD from "../coco-ssd/index";
import { DetectionState, PredictionWithImage, RootState } from "./types";

declare const cv: any;

const blurThreshold = 15;
const probabilityThreshold = 0.9;

function isOverThreshold(prob: any) {
  return prob.score > probabilityThreshold;
}

// TODO: pass classes as string[]
function isStateComplete(predictions: Array<CocoSSD.DetectedObject>): boolean {
    
    const capturedClasses = predictions.map(x => x.class);
    const containsClass = (c: string) => capturedClasses.some(x => x.includes(c));

    const isZpa = containsClass("zpa");
    const isSmns = containsClass("smns_value");
    const isDaisy = containsClass("daisy")
    const containsTariff = containsClass('_t1') || containsClass('_t2') || containsClass('_p');
    const containsValue = containsClass('value');
    const containsSn = containsClass("sn");

    if (isSmns && containsValue && containsSn) {
        return true;
    } else if ((isDaisy || isZpa) && containsValue && containsSn && containsTariff) {
        return true;
    } else {
        return false;
    }
}

// TODO: pass classes as string[]
function updateDetectionInfo(predictions: Array<CocoSSD.DetectedObject>, state: RootState): void {

    const capturedClasses = predictions.map(x => x.class);
    const containsClass = (c: string) => capturedClasses.some(x => x.includes(c));

    // DETECT/UPDATE DEVICE TYPE
    if (containsClass("smns_value")) {
        state.detectedDeviceType = "smns";
    } else if (containsClass("daisy")) {
        state.detectedDeviceType = "daisy";
    } else if (containsClass("zpa_type_l")) {
        state.detectedDeviceType = "zpa_l";
    } else if (containsClass("zpa_type_r")) {
        state.detectedDeviceType = "zpa_r";
    }
  
    // DETECT/UPDATE TARIFF
    if (containsClass("_t1") || containsClass("_t2") || containsClass("_p")) {
        const tariffClass = predictions.find(
            p =>
            p.class.includes("_p") ||
            p.class.includes("_t1") ||
            p.class.includes("_t2")
        );

        state.detectedTariffType = tariffClass?.class || "unknown";
    }
}

// TODO: what is the type of source image? can it be taken from canvas instead ?
function extractImages(
  predictions: Array<CocoSSD.DetectedObject>,
  sourceImage: any,
  component: any,
): Array<PredictionWithImage> {
  const currentSnCanvas = component.$refs.current_sn as any;
  const currentValueCanvas = component.$refs.current_value as any;
  const currentTariffCanvas = component.$refs.current_tariff as any;

  return predictions.map(prediction => {
    const extractedImage = extractImageFromPrediction(
      prediction,
      sourceImage,
      // scale image 0.5x if it's value (to avoid memory overflows)
      prediction.class == "value"
    );

    const isTariff =
      prediction.class.includes("_t1") ||
      prediction.class.includes("_t2") ||
      prediction.class.includes("_p");

    const isValue =
      prediction.class == "value" || prediction.class == "smns_value";
    const isSn = prediction.class == "sn";

    // check the type of predicted image and select different canvas based on the value
    let targetCanvas = null;
    if (isValue) {
      targetCanvas = currentValueCanvas;
    } else if (isSn) {
      targetCanvas = currentSnCanvas;
    } else if (isTariff) {
      targetCanvas = currentTariffCanvas;
    }

    // if known class is found -> render images to canvas and then convert it to blob
    if (targetCanvas !== null) {
      // render extracted image to canvas element
      cv.imshow(targetCanvas, extractedImage);

      // get base64 data from rendered image
      const base64image = targetCanvas
        .toDataURL("image/png")
        .replace("data:image/png;base64,", "");

      const image = document.createElement("image") as any;
      // FIXME: why bmp
      image.src = "data:image/bmp;base64," + base64image;
      document.body.appendChild(image);

      // convert base64 string data to blob (binary array)
      const imageBlob = makeblob(base64image, "image/png");
      extractedImage.delete();

      return [prediction, imageBlob];
    } else {
      extractedImage.delete();
      return [prediction, null];
    }
  }); // map(..)
}

function calculateDetectionState(predictionsAndImages: Array<PredictionWithImage>): DetectionState {

  const predictionMap: { [key: string]: PredictionWithImage } = {};
  const capturedClasses: Array<string> = predictionsAndImages.map(x => x[0].class);
  predictionsAndImages.forEach(prediction => {
    const [detectedObject, _] = prediction;
    predictionMap[detectedObject.class] = prediction;
  });
  const containsClass = (c: string) => capturedClasses.some(x => x.includes(c));

  const newState: DetectionState = {};

  // DETECT DEVICE TYPE (4 options)

  if (containsClass("smns_value")) {
    newState.deviceType = "smns";
  } else if (containsClass("daisy")) {
    newState.deviceType = "daisy";
  } else if (containsClass("zpa_type_l")) {
    newState.deviceType = "zpa_l";
  } else if (containsClass("zpa_type_r")) {
    newState.deviceType = "zpa_r";
  }

  // EXTRACT SERIAL NUMBER

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('sn')) {
    const [predicition, blob] = predictionMap['sn'];
    newState.sn = {
      score: predicition.score,
      image: blob!
    }
  }

  // EXTRACT VALUE

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('smns_value')) {
    const [predicition, blob] = predictionMap['smns_value'];
    newState.value = {
      score: predicition.score,
      image: blob!
    }
  }

  /* eslint-disable */
  if (predictionMap.hasOwnProperty('value')) {
    const [predicition, blob] = predictionMap['value'];
    newState.value = {
      score: predicition.score,
      image: blob!
    }
  }

  // EXTRACT TARIFF
  if (containsClass('_t1') || containsClass('_t2') || containsClass('_p')) {

    const tariffClass = predictionsAndImages.find( pi =>
      pi[0].class.includes('_p') ||
      pi[0].class.includes('_t1') ||
      pi[0].class.includes('_t2'));

    const [prediction, blob] = tariffClass!;

    newState.tariff = {
      score: prediction.score,
      image: blob!,
      class: prediction.class
    }
  }

  return newState;
}

function formIsComplete(state: RootState) {
  const snReady = state.form.sn !== "";
  const t1Ready = state.form.value_t1 !== "";
  const t2Ready = state.form.value_t2 !== "";
  const pReady = state.form.value_p !== "";
  const smnsValueReady = state.form.value_smns !== "";

  if (state.detectedDeviceType == 'smns' && smnsValueReady && snReady || 
      state.detectedDeviceType == 'daisy' && t1Ready && t2Ready && pReady && snReady ||
      state.detectedDeviceType == 'zpa_l' && t1Ready && t2Ready && pReady && snReady ||
      state.detectedDeviceType == 'zpa_r' && t1Ready && t2Ready && snReady) {
    return true
  } else {
    return false;
  }
}

function closeFullScreen(state: RootState) {
  state.fullscreen = false;
}

function isAlreadyCaptured(predictions: Array<CocoSSD.DetectedObject>, state: RootState) {
  const capturedClasses = predictions.map(x => x.class);
  const containsClass = (c: string) => capturedClasses.some(x => x.includes(c));

  const t1Ready = state.form.value_t1 !== "";
  const t2Ready = state.form.value_t2 !== "";
  const pReady = state.form.value_p !== "";
  const smnsReady = state.form.value_smns !== "";

  if (t1Ready && containsClass('_t1') || 
      t2Ready && containsClass('_t2') || 
      pReady && containsClass('_p') || 
      smnsReady && containsClass("smns_value")) {
    return true;
  } else {
    return false;
  }
}

export async function mainLogic(
  predictions: Array<CocoSSD.DetectedObject>,
  state: RootState,
  blurCoef: number,
  continuation: () => void,
  notify: (options: NotificationOptions | string) => void,
  renderTargetCanvas: any,
  sourceImage: any
) {

  const predictionsAreComplete =
    predictions.every(isOverThreshold) &&
    !isAlreadyCaptured(predictions, state) &&
    isStateComplete(predictions) &&
    blurCoef > blurThreshold;

  // only run analysis when image is clean and
  //predictions contain higher probabilities
  if (predictionsAreComplete) {

    // update detection info (device type / tariff) on the screen
    updateDetectionInfo(predictions, state);

    // TODO: try jpeg compressed format
    const base64data: string = renderTargetCanvas
      .toDataURL("image/png")
      .replace("data:image/png;base64,", "");
    const binaryString = window.atob(base64data);
    const len = binaryString.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }

    // display progress bar
    state.initialAnalysisInProgress = true;

    try {
      const response = await postRequestBinary(
        "https://web-cez-ocr-backend.azurewebsites.net/data",
        bytes,
        { "Content-Type": "application/octet-stream" }
      );

      const precisePredictions: Array<CocoSSD.DetectedObject> =
        response.data.predictions;

      const predictionsAndImages: Array<PredictionWithImage> = extractImages(
        precisePredictions,
        sourceImage,
        state
      );

      // calculate prediction state from predictions
      const detectionState = calculateDetectionState(predictionsAndImages);

      // disable progress bar
      state.initialAnalysisInProgress = false;

      // TODO: call Azure API (+update form, problaly need split in the future)
      await parseValuesFromImagesAndUpdateForm(detectionState, state, notify);

      if (formIsComplete(state)) {
        closeFullScreen(state);
      }

    } catch (err) {
      alert("cannot send data: " + err);
    } finally {
      // disable progress bar
      state.initialAnalysisInProgress = false;
      // continue in main capture loop
      continuation();
    }
  } else {
    //continue in main capture loop
    continuation();
  }
}


