import { DetectionState, FormState, RootState } from "@/views/types";
import $ from "jquery";

export async function postRequestBinary(
  url: string,
  data: any,
  headers: any
): Promise<any> {
  return new Promise((resolve, reject) => {
    $.ajax({
      url: url,
      headers: headers,
      type: "POST",
      contentType: "application/octet-stream",
      processData: false,
      data: data
    })
      .done(function(data, textStatus, jqXHR) {
        resolve({ data, textStatus, jqXHR });
      })
      .fail(function(jqXHR, textStatus, errorThrown) {
        reject({ jqXHR, textStatus, errorThrown });
      });
  });
}

async function postRequest(url: string, data: any, headers: any): Promise<any> {
  return new Promise((resolve, reject) => {
    $.ajax({
      url: url,
      headers: headers,
      type: "POST",
      processData: false,
      data: data
    })
      .done(function(data, textStatus, jqXHR) {
        resolve({ data, textStatus, jqXHR });
      })
      .fail(function(jqXHR, textStatus, errorThrown) {
        reject({ jqXHR, textStatus, errorThrown });
      });
  });
}

async function getRequest(url: string, headers: any): Promise<any> {
  return new Promise((resolve, reject) => {
    $.ajax({
      url: url,
      headers: headers,
      type: "GET"
    })
      .done(function(data, textStatus, jqXHR) {
        resolve({ data, textStatus, jqXHR });
      })
      .fail(function(jqXHR, textStatus, errorThrown) {
        reject({ jqXHR, textStatus, errorThrown });
      });
  });
}

async function createEmptyPromise(): Promise<undefined> {
  return new Promise(resolve => resolve(undefined));
}

async function wait(milliseconds: number): Promise<any> {
  return new Promise(resolve => {
    setTimeout(() => {
      resolve(undefined);
    }, milliseconds);
  });
}

function normalizeValue(value: string): string {
  const normalizeOne = value.replace(/[\\/|iI:]/g, "1");
  const normalizeZero = normalizeOne.replace(/[OQoD]/g, "0");
  const normalizeEight = normalizeZero.replace(/[B]/g, "8");
  const onlyDigits = normalizeEight.replace(/[^0-9]/g, "");
  return onlyDigits;
}

function isValidSerialNumber(value: string) {
  return /[0-9]{12}/.test(value);
}

function isValidZpaDaisyValue(value: string) {
  return /[0-9]{7}/.test(value);
}

function isValidSmnsValue(value: string) {
  return /[0-9]{6}/.test(value) || /[0-9]{7}/.test(value);
}

async function processImage(imgData: Blob): Promise<[object, string]> {
  const subscriptionKey = "0c20bfc48b9b487d8a2298030ecd7dc2";
  const endpoint = "https://cvbhdevocr.cognitiveservices.azure.com/";
  const uriBase = endpoint + "vision/v3.1/read/analyze";

  try {
    const { jqXHR } = await postRequest(uriBase, imgData, {
      "Content-Type": "application/octet-stream",
      "Ocp-Apim-Subscription-Key": subscriptionKey,
      "Access-Control-Allow-Headers": "*"
    });

    // "Operation-Location" in the response contains the URI
    // to retrieve the recognized text.
    const operationLocation = jqXHR.getResponseHeader("Operation-Location");

    // TODO: add 1.2 sec (?) interval and repeated retries
    await wait(1200);

    // Make the second REST API call and get the response.
    const { data } = await getRequest(operationLocation, {
      "Content-Type": "application/json",
      "Ocp-Apim-Subscription-Key": subscriptionKey,
      "Access-Control-Allow-Headers": "*"
    });

    // extract the guessed text from payload and return it as a result
    let rawValue = "";
    const analyzeResult = (data && data.analyzeResult) || {};

    try {
      const lines: Array<{ text: string }> =
        data.analyzeResult.readResults[0].lines;
      const normalizedLines = lines.map(value => normalizeValue(value.text));

      if (normalizedLines.length > 1) {
        // if multiple lines are returned pick the one with longest text length
        rawValue = normalizedLines.reduce(
          (a, b) => (a.length > b.length ? a : b),
          ""
        );
      } else {
        rawValue = normalizedLines[0];
      }
    } catch (err) {
      alert(
        "ERROR: text cannot be extracted from resulted JSON payload: " +
          JSON.stringify(data)
      );
    }

    // return both parsed text and original analyzis payload (object)
    return [analyzeResult, rawValue];
  } catch (e) {
    alert("ERROR during request: " + e + JSON.stringify(e));
    throw e;
  }
}

function updateForm(
  snText: string | undefined,
  valueText: string | undefined,
  tariffText: string | undefined,
  formState: FormState,
  detectionState: DetectionState,
  notify: (options: NotificationOptions | string) => void
): void {
  const dt = detectionState.deviceType;
  console.log("dt: ", dt);

  const preprocessValue = (value: string) => {
    if (dt == "daisy") {
      // take last 7 digits
      return value.slice(Math.max(value.length, 7) - 7);
    } else if (dt == "zpa_r") {
      // take first 7 digits
      return value.slice(0, 7);
    } else if (dt == "smns") {
      // take last 7 digits
      return value.slice(Math.max(value.length, 7) - 7);
    } else {
      return value;
    }
  };

  function preprocessSn(sn: string) {
    return sn.slice(Math.max(sn.length, 16) - 16);
  }

  // PREPROCESS ALL DATA

  let preprocessedValue = preprocessValue(valueText || "");
  console.log("preprocessedValue: ", preprocessedValue);

  const preprocessedSn = preprocessSn(snText || "");
  console.log(preprocessedSn, snText);

  // FIX: for ZPA/DAISY (7-digit code) -> add 1 at the end if length of code is only 6 digits
  if (detectionState.deviceType != "smns" && preprocessedValue.length == 6) {
    preprocessedValue += "1";
  }

  // FIX: for SMNS with SN (7-digit code) -> add 0 at the end if length of code is only 6 digits
  if (
    detectionState.deviceType == "smns" &&
    snText !== undefined &&
    preprocessedValue.length == 6
  ) {
    preprocessedValue += "0";
  }

  // FIX: for SMNS with SN (6-digit code) -> add 0 at the end if length of code is only 5 digits
  if (
    detectionState.deviceType == "smns" &&
    snText === undefined &&
    preprocessedValue.length == 5
  ) {
    preprocessedValue += "0";
  }

  // VALIDATE ALL DATA

  const snIsValid = isValidSerialNumber(preprocessedSn);
  let valueIsValid = false;

  if (detectionState.deviceType == "smns") {
    valueIsValid = isValidSmnsValue(preprocessedValue);
  } else {
    valueIsValid = isValidZpaDaisyValue(preprocessedValue);
  }

  notify({
    group: "notifications",
    type: valueIsValid ? "success" : "warn",
    title: "Capturing results",
    text: valueIsValid
      ? "Sucessfully captured device VALUE"
      : "Cannot capture device VALUE, please try again"
  } as any);

  // SN is optional in case of "smns" device, so do not display notification for it
  if (!(detectionState.deviceType == "smns" && snText == undefined)) {
    notify({
      group: "notifications",
      type: snIsValid ? "success" : "warn",
      title: "Capturing results",
      text: snIsValid
        ? "Sucessfully captured device SERIAL NUMBER"
        : "Cannot capture device SERIAL NUMBER, please try again"
    } as any);
  }

  // fill form with values

  // store serial number
  if (snIsValid) {
    formState.sn = preprocessedSn;
  }

  function padSmnsValue(value: string) {
    return (
      value.slice(0, value.length - 1) + "." + value.slice(value.length - 1)
    );
  }

  /* eslint-disable */
  // store value
  if (detectionState.deviceType == "smns" && valueIsValid) {
    formState.value_smns = padSmnsValue(preprocessedValue);
  } else if (detectionState.tariff !== undefined && valueIsValid) {
    const tc = detectionState.tariff.class;
    if (tc.includes("_t1")) {
      formState.value_t1 = preprocessedValue;
    } else if (tc.includes("_t2")) {
      formState.value_t2 = preprocessedValue;
    } else if (tc.includes("_p")) {
      formState.value_p = preprocessedValue;
    }
  }
}

export async function parseValuesFromImagesAndUpdateForm(
  detection: DetectionState,
  componentState: RootState,
  notify: (options: NotificationOptions | string) => void
): Promise<void> {

  componentState.azureAnalysisInProgress = true;

  // TODO: do not use type assertion -> pass safe object instead
  const snImageBlob: Blob | undefined = detection.sn?.image;
  const valueImageBlob: Blob = detection.value!.image;
  const tariffImageBlob: Blob | undefined = detection.tariff?.image;

  // run processing tasks in parallel
  const tasks = [snImageBlob, valueImageBlob, tariffImageBlob].map(blob => {
    if (blob !== undefined) {
      return processImage(blob);
    } else {
      return createEmptyPromise();
    }
  });

  // extract result from each task
  const results: Array<[object, string] | undefined> = [];

  try {
    for (const task of tasks) {
      results.push(await task);
    }
  } catch (err) {
    alert("Failed request: " + JSON.stringify(err));
  }

  const normalizedResults = results.map(res => res !== undefined ? res[1] : undefined);

  console.log("FULL RESULTS:", results);
  console.log("NORMALIZED RESULTS:", normalizedResults);
  
  componentState.azureAnalysisInProgress = false;

  // componentState.detectedDeviceType = detection.deviceType!;
  // componentState.detectedTariffType = detection.tariff?.class || "unknown"

  console.log("UPDATING FORM...");

  const [snText, valueText, tariffText] = normalizedResults;

  try {
    updateForm(snText, valueText, tariffText, componentState.form, detection, notify);
  } catch (err) {
    alert('cannot update form: ' + err + ', ' + JSON.stringify(err))
  }
}
